<template>
  <div class="page">
    <div class="content">
      <div class="articles">
        <ul class="list">
          <router-link tag="li" class="li" :to="'/lifes/'+item.id" v-for="(item,xh) in articles" :key="xh">
            <div class="title">{{item.title}}</div>
            <div class="time">{{item.time}}</div>
            <div class="img"><img :src="item.pic"></div>
          </router-link>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    name: 'articles',
    data() {
        return {
            articles: []
        }
    },
    created() {
        fetch('https://www.fastmock.site/mock/5f854ec44ed74b246903c6e75806ec14/api/life').then((rs) => {
            return rs.json()
        }).then((data) => {
            data.sort((a, b) => {
                let atime = a.time.replace(/-/g, '')
                let btime = b.time.replace(/-/g, '')
                if (atime > btime) {
                    return -1
                } else {
                    return 1
                }
            })
            this.articles = data
        })


    },
    methods: {},
    computed: {}
}
</script>

